<template>
  <div>
    <div class="div-top">
      <span class="title">车间成本分析</span>
    </div>
    <div class="div-content">
      <div class="div-frist">
        <span>选择周期</span>
        <el-select style="width: 96px;"  v-model="datetype">
            <el-option 
              v-for="item in cycleoptions"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>
        </el-select>
        <span style="margin-left: 10px;margin-right: 2px;">选择时间</span>
        <el-date-picker v-show="datetype=='year'" v-model="selectedyear" type="year" format="yyyy" value-format="yyyy"  placeholder="选择年"></el-date-picker>
        <el-date-picker v-show="datetype=='month'" v-model="selectedmonth" type="month" format="yyyy-MM" value-format="yyyy-MM"  placeholder="选择月"></el-date-picker>
        <el-date-picker v-show="datetype=='day'" v-model="selecteddate"  type="datetimerange" range-separator="至" start-placeholder="开始时间"  end-placeholder="结束时间" format="yyyy-MM-dd hh:mm:ss" value-format="yyyy-MM-dd hh:mm:ss"></el-date-picker>
        <span style="margin-left: 10px;margin-right: 2px;">选择区域</span>
        <el-select style="width: 96px;"  v-model="workshop">
          <el-option
            v-for="item in workshopoptions"
            :key="item.value"
            :label="item.label"
            :value="item.value">
          </el-option>
        </el-select>
        <el-button style="float: right;margin-right: 20px;" type="small" icon="el-icon-search" @click="search"></el-button>
      </div>

      <div class="div-second" >
        <template v-if="tableData">
        <el-table
          :data="tableData"
          border
          :span-method="arraySpanMethod"
        
          style="width: 100%;">
          <el-table-column class="table-column"
                           prop="name"
                           label="能源类型"
                           height="53"
                           width="200"
          >
            <template slot-scope="scope">
              <!--<i class="el-icon-time"></i>-->
              <!--<div class="scope-icon"></div>-->
              <span style="margin-left: 10px;float: left;">{{ scope.row.name }}</span>
            </template>
          </el-table-column>
          <el-table-column v-for="(item,index) in tableColumn" :key="index"
                           :prop="item.prop"
                           :label="item.lable"
                           height="53"
          >
            <el-table-column
              :prop="item.energy"
              label="能耗"
             >
            </el-table-column>
            <el-table-column
              :prop="item.cost"
              label="成本"
             >
            </el-table-column>
          </el-table-column>
        </el-table>
        </template>
      </div>
      <div class="div-c">
        <span class="title">车间能耗成本对比 | 电</span>
      </div>
      <div class="div-third">
        <div id="chart_energy" style="width: 100%;height:100%;"></div>
      </div>
    </div>
  </div>
</template>

<script>
  import echarts from "echarts";
  import Vue from "Vue";
  export default {
    data() {
      return {
        treeflag:false,//左侧是否加载树
         costtime:'',
        workfw:'',
        cycleoptions: [
          {value: 'day',label: '天'},
          {value: 'month',label: '月'},
          {value: 'year',label: '年' }
        ],
        datetype:"day",
        selectedyear:"",
        selectedmonth:"",
        selecteddate:'',
        workshopoptions:[
          {value: 'full',label: '全公司'},
          {value: 'one',label: '一分公司'},
          {value: 'two',label: '二分公司'},
          {value: 'three',label: '三分公司'},
        ],
        workshop:'',
        // second_height:400,
        // third_height:400,
        // divheight:600,
        tableColumn:[
          {
            lable:'制件1车间',
            prop:'1',
            energy:"energy1",
            cost:"cost1",
          },
          {
            lable:'制件2车间',
            prop:'2',
            energy:"energy2",
            cost:"cost2",
          },
          {
            lable:'制件3车间',
            prop:'3',
            energy:"energy3",
            cost:"cost3",
          },
          {
            lable:'制件4车间',
            prop:'4',
            energy:"energy4",
            cost:"cost4",
          }
          ,
          {
            lable:'制件5车间',
            prop:'5',
            energy:"energy5",
            cost:"cost5",
          },
          {
            lable:'制件6车间',
            prop:'6',
            energy:"energy6",
            cost:"cost6",
          }
        ],
        tableData:[],
        headerClickId:'',
      };
    },
    components: {},
    mounted() {
      let  that=this;
      // that.autoHeight();
      //that.initchart_energy();
      that.getmocktabledata();
      this.getechartdata();

    },
    methods: {
      // autoHeight() {
      //   let  that=this;
      //   var height = document.body.clientHeight-200;
      //   that.divheight=height;
      //   that.second_height=height*0.42;
      //   that.third_height=height*0.42;

      // },
      // cellClick(column, event){//ui说不需要了
      //   if(column.label !="能源类型" && column.level==1){
      //     this.headerClickId=column.id;
      //   }
      // },
      headercellstyle({row, column, rowIndex, columnIndex}){
        if(this.headerClickId){
          if(this.headerClickId==column.id){
            return 'border-top: 1px solid rgba(1,120,215,0.2);border-left: 1px solid rgba(1,120,215,0.2);border-right: 1px solid rgba(1,120,215,0.2);color: #0078D7;';
          }
          else if(column.id.indexOf(this.headerClickId) > -1){
            if(column.label=="能耗"){
              return 'border-left: 1px solid rgba(1,120,215,0.2);color: #0078D7;';
            }
            else  if(column.label=="成本"){
              return 'border-right: 1px solid rgba(1,120,215,0.2);color: #F5A623;';
            }
          }
          else {
            return '';
          }
        }
      },
      // cellStyle({row, column, rowIndex, columnIndex}){//ui说不需要列的点击
      //   if(this.headerClickId){
      //     if(column.id.indexOf(this.headerClickId) > -1){ //指定坐标
      //       if(column.label=="能耗"){
      //         if(row.name=="成本统计"){
      //           return 'border-bottom: 3px solid rgba(1,120,215,0.2);border-left: 1px solid rgba(1,120,215,0.2);border-right: 1px solid rgba(1,120,215,0.2);color: #F5A623;';
      //         }
      //         else{
      //           return 'border-left: 1px solid rgba(1,120,215,0.2);color: #0078D7;';
      //         }

      //       }
      //       else  if(column.label=="成本"){
      //         return 'border-right: 1px solid rgba(1,120,215,0.2);color: #F5A623;';
      //       }


      //     }else{
      //       return ''
      //     }
      //   }
      // },
      cellclassname({row, column, rowIndex, columnIndex}){
        if(this.headerClickId){
          if(column.id.indexOf(this.headerClickId) > -1){ //指定坐标
            if(column.label=="能耗"){
              if(row.name=="成本统计"){
                return  'cell-style-total';
              }
              else{
                return 'cell-style-content-left';
              }

            }
            else  if(column.label=="成本"){
              return  'cell-style-content-right';
            }


          }else{
            return ''
          }
        }
      },
      //合并单元格
      arraySpanMethod({ row, column, rowIndex, columnIndex }) {
        if(rowIndex==6){
          if(columnIndex===0){

          }
          else {
            if (columnIndex % 2 === 0) {
              return [0, 0];
            } else if (columnIndex % 2 === 1) {
              return [1, 2];
            }
          }


        }

      },
      //获得成本统计
      getSummaries(param) {
        const { columns, data } = param;
        const sums = [];
        columns.forEach((column, index) => {
          if (index === 0) {
            sums[index] = '统计';
            return;
          }
          const values = data.map(item => Number(item[column.property]));
          if (!values.every(value => isNaN(value))) {
            sums[index] = values.reduce((prev, curr) => {
              const value = Number(curr);
              if (!isNaN(value)) {
                return prev + curr;
              } else {
                return prev;
              }
            }, 0);
            sums[index] += '';
          } else {
            sums[index] = 'N/A';
          }
        });

        return sums;
      },
      getechartdata(){//获取mock echart动态数据
         
         var that = this;
            that.$http
            .get(this.BASE_INFO.BASE_URL+"api/cost/getworkcostchart",)
            .then(function(result) {
                result=result.data;
                 that.initchart_energy(result.data);
              
            });

      },
      //能耗对标
      initchart_energy(data){
         var option = {
           tooltip: {
             trigger: 'axis',
             axisPointer: {
               type: 'cross',
               crossStyle: {
                 color: '#999'
               }
             }
           },
          // legend: {
          //   data:['蒸发量','降水量','平均温度']
          // },
           xAxis: [
             {
               type: 'category',
               splitLine: {show:false},
               data :  function (){
                 var list = [];
                 for (var i = 1; i <= 6; i++) {
                   list.push('制件车' + i + '间');
                 }
                 return list;
               }()
             }
           ],
           yAxis: [
             {
               type: 'value',
               name: '能耗单位:kW.h',
               min: 0,
               max: 250,
               interval: 50,
               axisLabel: {
                 formatter: '{value}'
               }
             },
             {
               type: 'value',
               name: '成本单位:RMB',
               min: 0,
               max: 25,
               interval: 5,
               axisLabel: {
                 formatter: '{value}'
               }
             }
           ],
           series: [
             {
               name:'能耗',
               type:'bar',
               barWidth: 30,
               itemStyle: {
                 emphasis: {
                   barBorderRadius: 30
                 },
                 normal: {
                  // barBorderRadius: 30,
                  // barBorderRadius:[10, 10, 10, 10],
                   color: new echarts.graphic.LinearGradient(
                     0, 0, 0, 1,
                     [
                       {offset: 0, color: '#05CBFD'},
                       {offset: 1, color: '#6E8BFE'}

                     ]
                   )
                 }
               },
               data:data.nh
             },
             {
               name:'成本',
               type:'bar',
               barWidth: 30,
               itemStyle: {
                 emphasis: {
                  // barBorderRadius: 7
                 },
                 normal: {
                  // barBorderRadius: 7,
                   color: new echarts.graphic.LinearGradient(
                     0, 0, 0, 1,
                     [
                       {offset: 0, color: '#FAD961'},
                       {offset: 1, color: '#F76B1C'}

                     ]
                   )
                 }
               },
               data:data.cost
             }
           ]
         };
          var myChart = echarts.init(document.getElementById("chart_energy"));
          myChart.setOption(option);
      },
      search(){
        this.getmocktabledata();
        this.getechartdata();

      },  
      getmocktabledata(){//获取mock表格数据
          var that = this;
            that.$http
            .get(this.BASE_INFO.BASE_URL+"api/cost/getworkcostinfo",)
            .then(function(result) {
                result=result.data;
                 that.tableData=result.data;
                 that.updateTableData();
            });


      },
      updateTableData() {
        var total={};
        var tableColumn=this.tableColumn;
        total.name="成本统计";
        if(!this.tableData)
          return;
        this.tableData.map((v,i)=>{
           var item=v;
          var arr = Object.keys(item);
          for(var i in item){
            for(var index in tableColumn){
              if(arr[i] == tableColumn[index].energy){

              }
              if(arr[i]==tableColumn[index].cost){

              }
              Vue.set(total,tableColumn[index].energy,"¥36880.70");
              Vue.set(total,tableColumn[index].cost,"¥45690.00");
            }
          }

        });
        this.tableData.push(total);
      
      }  
    },
    computed:{
      
    },
  };


</script>

<style scoped>
  .div-top{
    height: 55px;
    border-bottom: 1px solid #d7d7d7;
  }
  .title{
    margin-top: 2px;
    font-family: 'PingFangSC-Regular';
    font-size: 16px;
    color: #333333;
    letter-spacing: 0.19px;
    font-weight:bold;
  }
  .btn-group{
     float: right;
     margin-right: 20px;
  }
  .div-content{

  }
  .div-frist{
    margin-top: 20px;

  }
  .div-second{
    margin-top: 10px;
     width: 100%;
    background-color: #F5F5F6;
    height:450px;
    overflow:auto;
    /*height: 400px;*/
  }
  .div-c{
    margin-top: 20px;
    height: 26px;
    background: rgba(1,120,215,0.10);
   
  }
  .div-third{
    margin-top: 20px;
    background-color: #F5F5F6;
    height:200px;
  }
  .scope-icon{
    float: left;
    width: 40px;
    height: 4px;
    background-color: #F5A623;
    border: 1px solid  #F5A623;
    margin-top: 10px;
  }
  .el-table th, .el-table tr {
    background-color: #F5F5F6;
    border: 1px solid #ffffff;
  }
  .el-table--enable-row-transition .el-table__body td{
    border: 1px solid #ffffff;
  }

  .cell-style-content-left{
   border-left: 1px solid rgba(1,120,215,0.2);
  }
  .cell-style-content-right{
    border-right: 1px solid rgba(1,120,215,0.2);
  }
  .cell-style-total{
    border-bottom: 3px solid rgba(1,120,215,0.2);border-left: 1px solid rgba(1,120,215,0.2);border-right: 1px solid rgba(1,120,215,0.2);
  }

</style>




